import React, { useState, useEffect } from 'react';
import tea from './Assets/tea.png'

import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import WaterDropIcon from '@mui/icons-material/Opacity';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import InfoOutlinedIcon from '@mui/icons-material/InfoOutlined';
import ListItemButton from '@mui/material/ListItemButton';

import AccordionDetails from '@mui/material/AccordionDetails';
import CardContent from '@mui/material/CardContent';
import Card from '@mui/material/Card';
import Stack from '@mui/material/Stack';
import SwipeableDrawer from '@mui/material/SwipeableDrawer';
import LinearProgress  from '@mui/material/LinearProgress';

import SolarIcon from '@mui/icons-material/SolarPower';
import FireIcon from '@mui/icons-material/LocalFireDepartment';
import HandIcon from '@mui/icons-material/PanTool';
import AirIcon from '@mui/icons-material/Air';
import ThermostatIcon from '@mui/icons-material/Thermostat';
import OutdoorGrillIcon from '@mui/icons-material/OutdoorGrill';
import SignLanguageIcon from '@mui/icons-material/SignLanguage';
import Tooltip from '@mui/material/Tooltip';
import BubbleChartIcon from '@mui/icons-material/BubbleChart';
import BlurCircularIcon from '@mui/icons-material/BlurCircular';
import WavesIcon from '@mui/icons-material/Waves';

import Dialog from '@mui/material/Dialog';

function MapAccordion(props) {
  const [dialogOpen, setDialogOpen] = React.useState(false);
  const [teaTypeIndex, setTeaTypeIndex] = React.useState(0);
  const [progress, setProgress] = React.useState(0);
  const [buffer, setBuffer] = React.useState(10);  

  const handleMouseEnter = (e) =>{
    console.log("mouse entered", JSON.stringify(e.target.getAttribute("regionName")))
    // console.log(props)
    props.setRegionHovered(e.target.getAttribute("regionName"))
  }

  const handleMouseLeave = () =>{
    // console.log("mouse entered")
  }
// 

function LinearProgressWithLabel(props) {
  return (
    <Box sx={{ display: 'flex', alignItems: 'center' }}>
      <Box sx={{ width: '100%', mr: 1 }}>
        <LinearProgress sx={{
                  backgroundColor:'darkgray',
                  '& .MuiLinearProgress-bar': {
                    backgroundColor: 'brown'
                  }
                }}
        variant="determinate" {...props} />
      </Box>
      <Box sx={{ minWidth: 35 }}>
        <Typography variant="body2" color="text.primary"><b>{`${Math.round(
          props.value,
        )}%`}</b></Typography>
      </Box>
    </Box>
  );
}

  const processes = [
    <Tooltip title="Sun Drying"><SolarIcon sx={{fontSize:'45px', color:"darkgoldenrod", mr:1}}/></Tooltip>, 
    <Tooltip title="Rolling"><HandIcon sx={{fontSize:'45px', color:"burlywood", mr:1}}/></Tooltip>,
    <Tooltip title="Baking"><ThermostatIcon sx={{fontSize:'45px', color:"lightcoral", mr:1}}/></Tooltip>,
    <Tooltip title="Heap Fermentation"><BubbleChartIcon sx={{fontSize:'45px', color:"lightblue", mr:0}}/></Tooltip>,
    <Tooltip title="Baking by Fire"><FireIcon sx={{fontSize:'45px', color:"firebrick", mr:1}}/></Tooltip>,
    <Tooltip title="Withering"><BlurCircularIcon sx={{fontSize:'45px', color:"darkgreen", mr:1}}/></Tooltip>,
    <Tooltip title="Fixing by Pan"><OutdoorGrillIcon sx={{fontSize:'45px', color:'black', mr:1}}/></Tooltip>,
    <Tooltip title="Air Drying"><AirIcon sx={{fontSize:'45px', color:'lightblue', mr:1}}/></Tooltip>,
    <Tooltip title="Yellowing"><WavesIcon sx={{fontSize:'45px', color:'gold', mr:1}}/></Tooltip>,
    ];

  const teaTypeInfo = [
    {
      "Type":"Oolong",
      "Oxidation": "20-80%",
      "Feature": "RouNian, Baking",
      "FeatureIcons":[0,1,2],
      "Oxidation":20,
      "Description":"Oolong tea, a semi-oxidized delight, follows a meticulous production process. Plucked leaves undergo withering, bruising, and partial oxidation, skillfully halted at various stages. Shaping and firing complete the transformation, resulting in a range of flavors between green and black teas. Oolong's nuanced taste and aroma make it a revered tea variety.",
    },
    {
      "Type":"Dark",
      "Oxidation": "100%",
      "Feature": "Fermentation",
      "FeatureIcons":[5, 6, 0, 3],
      "Oxidation":100,
      "Description":"Dark tea, an aged and fermented marvel, begins with sun-dried leaves. A unique process of microbial fermentation (wo dui) transforms the leaves over time. Post-fermentation, the tea is aged in humid conditions, developing complex flavors and earthy undertones. The result is a distinct and prized dark tea, cherished for its richness.A tea type",
    },
    {
      "Type":"White",
      "Oxidation": "20%",
      "Feature": "Sun Drying",
      "FeatureIcons":[5, 7],
      "Oxidation":0,
      "Description":"White tea production starts with delicate buds and young leaves handpicked. Minimal processing involves withering under sunlight or controlled conditions to allow natural drying. This gentle process maintains the leaves' natural appearance and subtle flavors. The result is a rare and delicate white tea, celebrated for its mild taste and health benefits.",
    },
    {
      "Type":"Green",
      "Oxidation": "20%",
      "FeatureIcons":[5, 6, 1, 7],
      "Oxidation":0,
      "Description":"Green tea production begins with hand-picking tender leaves. \
      Swift steaming or pan-frying halts oxidation, preserving its natural color and nutrients. \
      Rolling and shaping follow to enhance flavor and appearance. \
      The leaves are then dried to prevent further oxidation. Meticulous processing results \
      in the creation of the vibrant and refreshing green tea.",
    },
    {
      "Type":"Red (Black)",
      "Oxidation": "80%",
      "Feature": "RouNian, Baking",
      "FeatureIcons":[5, 1, 3, 2 ],
      "Oxidation":80,
      "Description":"Black tea production involves plucking young tea leaves, \
      followed by withering to reduce moisture. Leaves undergo rolling to release enzymes,\
       initiating oxidation. Fermentation transforms the leaves, enhancing flavor and color.\
        Drying halts oxidation, creating the distinctive dark hue.",
    },
    {
      "Type":"Yellow",
      "Oxidation": "20%",
      "Feature": "RouNian, Baking",
      "FeatureIcons":[5, 6, 8, 1, 7],
      "Oxidation":10,
      "Description":
      "A rare and exquisite variety of tea where its leaves undergo a unique oxidation process. \
      With a delicate flavor profile, it offers a harmonious blend of sweet and floral notes. Renowned for its distinct golden hue, yellow tea provides a rare and refined tea-drinking experience."
    },
  ]; 

	return (
        <Accordion
        expanded={props.expanded === props.panelId}
        onChange={(e, isExpanded) => props.handleChange(props.panelId, isExpanded, e.target.id)}
        // onChange={(e) => console.log(e.target.id)}
         disableGutters
        >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
        >
          <Typography sx={{ width: '60%', flexShrink: 0 }}>
            <b>{props.summary}</b>
          </Typography>
          <WaterDropIcon sx={{color:props.dropColor, mr:2}} />
          <InfoOutlinedIcon id="infoIcon" sx={{color:"gray"}}
          onClick={() => setDialogOpen(true)} />
        </AccordionSummary>
        <AccordionDetails>
        <List sx={{mt:'0px'}}  disablePadding dense>

        {props.teas.map((value, index) => {
          return (
            <ListItem disablePadding>
            
            {/* <ListItemButton onClick={() => setTeaType("PuErh")}>  */}
              <ListItemButton
              regionName={value.region}
              onClick={() => props.handleSelect(value.region, value.index)}
              onMouseEnter={handleMouseEnter}
              onMouseLeave={() => props.setRegionHovered(null)}
              > 
                <ListItemText primary={value.name} secondary={value.region} />
              </ListItemButton>
              
            </ListItem>
          )
        })}
        </List>

        <Dialog open={dialogOpen} onClose={() => setDialogOpen(false)}> 
          <Card sx={{width: 350, minHeight: 300}} >
            <CardContent>
              <Stack spacing={1}>
                <Typography variant="h6">
                <b>  {teaTypeInfo[props.typeIndex]["Type"] + " Tea"}</b>
                </Typography>
                <Typography variant="body1">
                  <b>Process:</b>
                  <Box sx={{fontSize:'45px'}}>
                    {teaTypeInfo[props.typeIndex].FeatureIcons.map((val, index) => processes[val])}
                  </Box>
                </Typography>

                <Typography variant="body1">
                <b>Oxidation:</b>
                </Typography>
                {teaTypeInfo[props.typeIndex].Type === "Oolong" && <Typography>From</Typography>}
                <LinearProgressWithLabel variant="determinate" value={teaTypeInfo[props.typeIndex].Oxidation}/>
                {teaTypeInfo[props.typeIndex].Type === "Oolong" && <Typography>To</Typography>}
                {teaTypeInfo[props.typeIndex].Type === "Oolong" &&
                 <LinearProgressWithLabel variant="determinate" value={80}/>
                }
                <Typography variant="body1">
                  <b>Description: </b>
                </Typography>

                <Typography variant="body1">
                {teaTypeInfo[props.typeIndex].Description}
                </Typography>
              </Stack>

            </CardContent>
          </Card>
        </Dialog>

        </AccordionDetails>
      </Accordion>
	)
}

export default MapAccordion;